/* Start Our Framework */

.container {
	width: 1100px;
	margin: auto;
	position: relative;
}

.text-center {
	text-align: center;
}

.upper { text-transform: uppercase; }

.main-color { color: #f7600e; }

.main-background { background-color: #f7600e; }

.overlay {
    background: rgba(49 , 55 , 61 , .8);
    width: 100%;
    height: 100%;
}

.hidden { display: none; }

button {
	cursor: pointer;
	outline: inherit;
}

