/* End Our Framework */


/* Start Header */
.header {
	background-image: url('../public/images/header-unsplash-1080x675.jpg');
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
	color: #fff;
 	height: 625px;
}

.header .navbar {
	overflow: hidden;
	padding: 40px 0;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
}

.header .navbar span { font-size: 20px; }

.header .navbar .logo { font-size: 24px; }

.header .navbar .links {
	float: right;
	cursor: pointer;
}

.header .navbar .links i {
	margin-left: 5px;
}

.header .intro {
	padding-top: 190px;
	/*
    display: table-cell;
	vertical-align: middle;
	*/
}

.header .intro h1 {
	margin: 0;
	font-size: 50px;
	font-weight: 800;
}

.header .intro p {
	color: #e2e2e2;
	font-size: 20px;
	margin: 10px 0 40px;
	line-height: 1.8;
}

.header .buttons button {
	background: transparent;
	color: #eee;
	width: 160px;
	padding: 10px 0;
	margin: 0 10px;
	border: 1px solid #909090;
	border-radius: 10px;
	font-size: 15px;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.header .buttons button:hover {
	background-color: #f7600e;
	color: #fff;
	border: 1px solid #f7600e;
}

.header .arrow {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	text-align: center;
	padding: 35px 0;
}

.header .arrow i {
	padding: 5px;
	width: 30px;
	height: 30px;
	line-height: 25px;
    border: 2px solid #999;
	border-radius: 50%;
	cursor: pointer;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.header .arrow i:hover {
	color: #f7600e;
}
/* End Header */


/* Start Features */
.features {
	padding: 80px 0;
	overflow: hidden;
}

.features .box {
	width: 22%;
	float: left;
	margin-right: 4%;
}

.features .box:last-child {
	margin-right: 0;
}

.features .box i {
	color: #f7600e;
	border: 1px solid #999;
	width: 110px;
	height: 110px;
	line-height: 110px;
	border-radius: 50%;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.features .box i:hover {
	background-color: #f7600e;
	border: 1px solid #f7600e;
	color: #fff;
}

.features .box h3 {
	text-transform: uppercase;
	font-weight: normal;
	color: #505050;
}

.features .box p {
	line-height: 1.8;
	color: #888;
}
/* End Features */

/* Start Subscribe */
.subscribe {
	background-image: url('../public/images/sub.jpg');
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}

.subscribe form {
	padding: 100px 0;
	margin: auto;
	width: 700px;
	text-align: center;
}

.subscribe form input[type="email"] {
	padding: 0 10px;
	width: 320px;
	border: none;
	border-radius: 10px;
	font-size: 20px;
	position: relative;
	bottom: 2;
	height: 40px;
	line-height: 40px;
	outline: inherit;
}

.subscribe form button {
	padding: 12px;
	width: 200px;
	border: none;
	border-radius: 10px;
	background-color: #f7600e;
	color: #fff;
	font-size: 15px;
	margin-left: 25px;
}

.subscribe form input[placeholder] {
	font-size: 14px;
}

