}

.subscribe ::-ms- input[placeholder] {
    font-size: 10px;
}
*/
/* End Subscribe */



/* Start Our Work */
.our-work {
	padding: 50px 0;
}

.our-work h2 {
    font-weight: 500;
    font-size: 35px;
    margin-bottom: 0;
}

.our-work h2 ~ p {
    line-height: 2;
    margin: 10px 0 40px;
}

.our-work .show-more {
	font-size: 17px;
    background: transparent;
    border: 1.5px solid #ccc;
    border-radius: 10px;
    width: 180px;
    padding: 11px;
    margin-top: 20px;
    color: #555;
    -webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.our-work .show-more:hover {
	background-color: #f7600e;
	border: 1.5px solid #f7600e;
	color: #fff;
}

.our-work .items-box {
	overflow: hidden;
}

.our-work .items-box .item {
	float: left;
	overflow: hidden;
	width: 30%;
	margin-left: 3%;
	margin-bottom: 15px;
	border-radius: 10px;
	-webkit-transition: all .75s ease-in-out;
	-moz-transition: all .75s ease-in-out;
	transition: all .75s ease-in-out;
	position: relative;
}

.our-work .items-box .item:hover .over {
	display: block;
}

.our-work .items-box .item .over {
	position: absolute;
	overflow: hidden;
	top: 0;
	left: 0;
	width: 350px;
	height: 250px;
	background-color: rgba(247, 96, 14, .9);
	color: #fff;
	display: none;
}

.our-work .item .over h3 {
	margin-top: 50px;
	margin-bottom: 0;
}

.our-work .item .over p {
	margin-top: 10px;
	margin-bottom: 30px;
	line-height: 2;
}

.our-work .item .over button {
	background-color: transparent;
	border: 1px solid #fff;
	color: #fff;
	width: 180px;
	padding: 10px;
	border-radius: 10px;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.our-work .item .over button:hover {
	background-color: #fff;
	border: 1px solid #fff;
	color: #f7600e;
}
/* End Our Work */

/* Start Testimonials */
.testimo {
	background: url('../public/images/testimonials.jpg');
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}

.testimo .client {
	padding: 50px 0;
	color: #fff;
	font-size: 20px;
}

.testimo .client img {
	width: 165px;
	height: 165px;
	border-radius: 50%;
}

.testimo .client p {
	line-height: 2;
	font-style: italic;
}

.testimo .fa-chevron-right,
.testimo .fa-chevron-left {
	position: absolute;
	top: 50%;
	color: #999;
	cursor: pointer;
}

.testimo .fa-chevron-right:hover ,
.testimo .fa-chevron-left:hover {
	color: #f7600e;
}

.testimo .fa-chevron-right {
	right: 0;
}

.testimo .fa-chevron-left {
	left: 0;
}
/* End Testimonials */

/* Start Our Team */
.our-team {
	padding: 50px 0;
}

.our-team h2 {
	font-weight: 500;
    font-size: 35px;
    margin-bottom: 0;
}

.our-team .main {
    margin: 10px 0 40px;
    line-height: 2;
    color: #666;
    font-size: 20px;
}

.our-team .team {
	overflow: hidden;
}

.our-team .team .person {
	float: left;
	width: 24%;
	margin-left: 1%;
	color: #888;
}

.our-team .team .person:hover img {
	filter: none;
	-webkit-filter: grayscale(0);
}

.our-team .team .person:hover {
	color: #555;
}

.our-team .team .person:hover .fa-facebook-f {
	background-color: #3b5998;
}

.our-team .team .person:hover .fa-twitter {
	background-color: #55acee;
}

.our-team .team .person:hover .fa-google-plus {
	background-color: #dc4e41;
}

.our-team .team .person img {
	width: 200px;
	height: 250px;
	filter: gray;
	filter: grayscale(1);
	-webkit-filter: grayscale(1);
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.our-team .team .person h3 {
	margin: 15px;
	margin-bottom: 5px;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.our-team .team .person p {
	font-size: 15px;
}

.our-team .team .person .social-icons i {
    background-color: #9da1a5;
    color: #fff;
	margin-right: 5px;
    width: 25px;
    height: 25px;
    line-height: 28px;
    border-radius: 50%;
    cursor: pointer;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}
/* End Our Team */

/* Start Contact Us */
.contact {
	background-image: url('../public/images/sub.jpg');
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}

.contact .overlay {
	padding: 50px 0;
    color: #fff;
}

.contact .overlay h2 {
	font-weight: 500;
	font-size: 35px;
}

.contact .overlay form {
	width: 700px;
	overflow: hidden;
	margin: 50px auto;
	text-align: center;
}

.contact .overlay form input[type="email"],
.contact .overlay form input[type="text"] {
	width: 45%;
	padding: 0 10px;
	height: 40px;
	line-height: 40px;
	border: 1px solid #ccc;
	border-radius: 10px;
	outline: inherit;
}

.contact .overlay form input[type="email"] {
	float: left;
}

.contact .overlay form input[type="text"] {
	float: right;
}

.contact .overlay form textarea {
	width: 100%;
	margin-top: 20px;
	padding: 10px;
	height: 140px;
	border: 1px solid #ccc;
	border-radius: 10px;
	outline: inherit;
}

.contact .overlay form .info {
	overflow: hidden;
}

.contact form .info .form-icons {
	float: right;
	margin-top: 20px;
}

.contact .overlay form .info button {
	float: left;
	padding: 10px;
	margin-top: 20px;
	/*
	background-color: transparent;
    border: 1px solid #fff;
	*/
	background-color: #f7600e;
	border: 1px solid #f7600e;
	border-radius: 10px;
	color: #fff;
	width: 200px;
	font-size: 15px;
	-webkit-transition: all .5s ease-in-out;
	-moz-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.contact .overlay form .info button:hover {
	background-color: transparent;
	border: 1px solid #f7600e;
	color: #fff;
}

.contact form .info .form-icons i {
	background-color: #9da1a5;
    color: #fff;
    margin-right: 10px;
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 50%;
    cursor: pointer;
}

.contact form .info .form-icons i:last-of-type {
	margin-right: 0;
}

.contact form .info .form-icons .fa-facebook-f {
	background-color: #3b5998;
}

.contact form .info .form-icons .fa-twitter {
	background-color: #55acee;
}

.contact form .info .form-icons .fa-google-plus {
	background-color: #dc4e41;
}

.contact form .info .form-icons .fa-pinterest-p {
	background-color: #e60023;
}
/* End Contact Us */


/* Start Footer */
.footer {
	background-color: #3b434c;
	color: #fff;
	padding: 15px 0;
	overflow: hidden;
}

.footer .copyright {
	float: left;
}

.footer .design {
	float: right;
}
/* End Footer */


